<template>
  <div class="detail">
    <div class="status">
      <div class="container">
          <div class="jobInfo">
            <p class="jobStatus">招聘中</p>
            <p><span class="jobName">{{data.positionName}}</span><span class="jobMoney">{{data.salaryBegin}}-{{data.salaryEnd}}K</span></span></p>
            <p class="jobNeed"><span>{{data.workCity}}</span> | <span>{{getExpReq(data.expReq)}}</span> | <span>{{getEduReq(data.eduReq)}}</span></p>
          </div>
          <div class="apply">
           <button :disabled="data.deliverFlag === 1">{{com.deliverFlag === 0 ? '投递职位':'已投递'}}</button>
          </div>
      </div>
    </div>
    <div class="container">
        <bread :data="[{name:'首页',url:'/'},{name:'职位招聘',url:''},{name:'专业不限'.name,url:''}]"></bread>
    </div>
    <companyInfo :data="data" :com="com"></companyInfo>
  </div>
</template>
<script>
import api from '../api'
import companyInfo from './compoments/companyInfo'
import bread from './compoments/bread'
export default {
  name:'CompanyDetail',
  data(){
    return{
      data:{},
      com:{},
    }
  },
  components:{
    companyInfo,
    bread
  },
  mounted() {
    this.init()
  },
  methods: {
    init(){
      api.get('http://47.112.170.125:8080/env/position/getPositionById/'+this.$route.query.id
      ).then(res=>{
        this.data = res
         api.post('http://47.112.170.125:8080/env/company/getByIdOrUserId?id='+res.comId,
          {}
          ).then(resp=>{
            this.com = resp;
          })
      })
    },
     getExpReq(val){
      let newVal
      switch(val){
        case 0:
        newVal = '不限'
        break;
        case 1:
        newVal = '1-3年'
        break;
        case 2:
        newVal = '3-5年'
        break;
        case 3:
        newVal = '5-10年'
        break;
        case 4:
        newVal = '10年以上'
        break;
        default:
        break;
      }
      return newVal;
    },
    getEduReq(val){
      let newVal
      switch(val){
        case 0:
        newVal = '不限'
        break;
        case 1:
        newVal = '中专及以上'
        break;
        case 2:
        newVal = '大专及以上'
        break;
        case 3:
        newVal = '本科及以上'
        break;
        case 4:
        newVal = '硕士及以上'
        break;
        case 5:
        newVal = '博士及以上'
        break;
        default:
        break;
      }
      return newVal;
    },
  },
}
</script>
<style lang="less">
.detail{
  .bread{
    text-align: left;
  }
  .status{
    background-color: #454c5f;
    height: 180px;
    width: 100%;
    .jobInfo{
        display: inline-block;
        width: 600px;
        float: left;
      p{
        text-align: left;
        color: #fff;
        font-size: 18px;
        padding: 20px;
        &.jobStatus{
          font-size:14px
        }
        &.jobNeed{
          font-size: 14px;
        }
        .jobMoney{
          margin-left:40px ;
          color:#F45C2C
        }
      }
    }
    .apply{
      float: right;
        button{
          margin-top:70px;
          height: 36px;
          line-height: 36px;
          background-color: #10bbb8;
          outline: 0 none;
          border:0 none;
          font-size:18px;
          text-align: center;
          width: 120px;
          color: #fff;
          border-radius: 3px;
          &:hover{
            cursor: pointer;
          }
        }
    }
  }
}
  
</style>


